import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class YouTubeService {
  private apiUrl = 'https://www.googleapis.com/youtube/v3/commentThreads';
  private videoUrl = 'https://www.googleapis.com/youtube/v3/videos';

  constructor(private http: HttpClient) { }

  getVideoDetails(videoId: string, apiKey: string): Observable<any> {
    const params = {
      part: 'snippet',
      id: videoId,
      key: apiKey
    };
    return this.http.get(this.videoUrl, { params });
  }

  getComments(videoId: string, apiKey: string): Observable<any> {
    const params = {
      part: 'snippet,replies',
      videoId: videoId,
      key: apiKey,
      textFormat: 'plainText',
      maxResults: '200'
    };
    return this.http.get(this.apiUrl, { params });
  }
}
